<template>
    <div class="experience">
        <p class="content-title">{{info.title}}</p>
        <transition
        appear
        appear-active-class="custom-appear-active-class"
        >
            <div class="content-container">
                <div class="content" v-rock>
                    <div class="experience-item">
                        <p class="title">{{experience.title}}</p>
                        <time>{{experience.time}}</time>
                        <div class="info">
                            <p class="post">{{experience.post}}</p>
                            <span>{{experience.info}}</span>
                        </div>
                    </div>
                    <div class="switch">
                        <span
                        v-for='item in experienceArr'
                        :key='item.id'
                        @click.stop='changePage(item.id)'
                        :class="{'active':item.id==$route.params.id, 'prompt':item.id!=$route.params.id}"
                        ></span>
                    </div>
                </div>
                <div class="switch-year">
                    <a href="javascript:;"
                    v-for='(year,index) in info.data'
                    :key='index'
                    @click='changeYear(year.year)'
                    :class="{'active':year.year==$route.params.year}"
                    >{{year.year}}</a>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
const url = 'https://www.easy-mock.com/mock/5c9b940ca88cf9475e399780/resume/experience'
export default {
    data() {
        return {
            info: {},
            experience: {},
            experienceArr: []
        }
    },
    created() {
        this.$http(url).then(({ data }) => {
            this.info = data
            this.getExperience()
        })
    },
    watch: {
        $route() {
            this.getExperience()
        }
    },
    methods: {
        getStyle(obj, attr) {
            return getComputedStyle(obj)[attr]
        },
        getScale(scale) {
            if (scale > 1) {
                return (scale - 1)
            } else {
                return -scale
            }
        },
        changeYear(year) {
            this.$router.push({
                name: this.$route.name,
                params: { year: year, id: 1 }
            })
        },
        changePage(id) {
            this.$router.push({
                name: this.$route.name,
                params: { year: this.$route.params.year, id: id }
            })
        },
        getExperience() {
            const firstIndex = this.info.data.findIndex(item => item.year === Number(this.$route.params.year))
            this.experienceArr = this.info.data[firstIndex].data
            const secondIndex = this.experienceArr.findIndex(item => item.id === Number(this.$route.params.id))
            this.experience = this.experienceArr[secondIndex]
        }
    },
    computed: {
        isMobile() {
            return /Mobile/.test(navigator.userAgent)
        }
    },
    directives: {
        rock: {
            inserted(el, binding, vnode) {
                const vm = vnode.context
                const center = {
                    x: el.offsetLeft + parseInt(vm.getStyle(el, 'width')) / 2,
                    y: el.offsetTop + parseInt(vm.getStyle(el, 'height')) / 2
                }
                const move = 'mousemove'
                const end = 'mouseout'
                el.addEventListener(move, function(e) {
                    const scale = { x: vm.getScale(e.pageX / center.x), y: vm.getScale(e.pageY / center.y) }
                    let { x, y } = scale
                    this.style.transition = this.style.webkitTransition = '.8s transform'
                    this.style.transform = this.style.webkitTransform = `translate3D(${Math.floor(x * 10)}px,0,0)
                        rotateX(${Math.floor(-y * 2)}deg)
                        rotateY(${Math.floor(x * 2)}deg)
                    `
                })
                el.addEventListener(end, function() {
                    this.style.transform = `translate3D(0,0,0) `
                })
            }
        }
    }
}
</script>

<style lang='less' scoped>
@import '../common/style/common.less';
    @keyframes bounceIn{
        0%{opacity: 0;transform: scale(.2)}
        20%{transform: scale(1.1)}
        40%{transform: scale(.9)}
        60%{opacity: 1;transform: scale(1.03)}
        80%{transform: scale(.97)}
        100%{transform: scale(1)}
    }
    .custom-appear-active-class{
        animation: bounceIn 1s;
    }
    @keyframes down{
        100%{transform: translate3d(0,8px,0)}
    }
    .experience{
        .position;
        background:url(../assets/bg/bg.png),rgb(2, 156, 125);
    }
    .content-title{
        animation-duration: .8s;
        color: #fff;
        &::after{
            background: #fff;
        }
    }
    .content-container{
        perspective: 600px;
        padding-top: 30px*@scale;
        margin:0 25px*@scale;
        font-family: '微软雅黑';
    }
    .content{
        background: #fff;
        box-sizing: border-box;
        position: relative;
        box-shadow: 0 0 10px*@scale #000;
        border-radius: 5px*@scale;
        .switch{
            position: absolute;
            bottom: 5px*@scale;
            text-align: center;
            width: 100%;
            line-height: 12px*@scale;
            span{
                width: 12px*@scale;
                height: 12px*@scale;
                background:#000;
                display: inline-block;
                margin: 0 15px*@scale;
                border-radius: 50%;
                position: relative;
                &.active{
                    background: #7c179b;
                }
                &.prompt::after{
                    content: '';
                    position: absolute;
                    left: -2px;
                    top: -20px;
                    width: 0;
                    border: 8px solid transparent;
                    border-top-color: rgb(182, 182, 182);
                    animation: 1.2s down infinite alternate linear;
                }
            }
        }
    }
    .experience-item{
        padding:20px*@scale 20px*@scale 50px*@scale 20px*@scale;
        .title{
            font-weight: 600;
            line-height: 40px*@scale;
            font-size: 18px*@scale;
        }
        time{
            font-size: 12px*@scale;
            line-height: 30px*@scale;
        }
        .info{
            .post{
                font-size: 14px*@scale;
                line-height: 35px*@scale;
                color: #7c179b;
            }
            span{
                color: #999999;
                font-size: 13px*@scale;
            }
        }
    }
    .switch-year{
        margin-top: 40px*@scale;
        text-align: center;
        a{
            display: inline-block;
            width: 40px*@scale;
            height: 20px*@scale;
            line-height: 20px*@scale;
            background: #525151;
            color: #fff;
            margin: 0 5px*@scale;
            text-align: center;
            font-size: 12px*@scale;
            transition: transform .3s ease-in-out;
            border-radius:5px;
            &:hover{
                transform: translate3d(0,-5px,0);
            }
            &.active{
                background: #7c179b;
            }
        }
    }
    @media all and (min-width:375px){
        .content-container{
            padding-top: 100px;
        }
    }
    @media all and (min-width:768px){
        @scale:1.4;
        .content-container{
            padding-top: 80px;
            margin:0 25px*@scale;
        }
        .content{
            box-shadow: 0 0 10px*@scale #000;
            border-radius: 5px*@scale;
            .switch{
                bottom: 5px*@scale;
                line-height: 12px*@scale;
                span{
                    width: 12px*@scale;
                    height: 12px*@scale;
                    margin: 0 15px*@scale;
                    &.prompt::after{
                        left: -2px;
                        top: -20px;
                        width: 0;
                        border: 10px solid transparent;
                        border-top-color: rgb(182, 182, 182);
                    }
                }
            }
        }
        .experience-item{
            padding:20px*@scale 20px*@scale 50px*@scale 20px*@scale;
            .title{
                line-height: 40px*@scale;
                font-size: 18px*@scale;
            }
            time{
                font-size: 12px*@scale;
                line-height: 30px*@scale;
            }
            .info{
                .post{
                    font-size: 14px*@scale;
                    line-height: 35px*@scale;
                }
                span{
                    font-size: 13px*@scale;
                }
            }
        }
        .switch-year{
            margin-top: 40px*@scale;
            a{
                width: 40px*@scale;
                height: 20px*@scale;
                line-height: 20px*@scale;
                margin: 0 5px*@scale;
                font-size: 12px*@scale;
            }
        }
    }
    @media all and (min-width:960px){
        @scale:1;
        .content-container{
            padding-top: 80px;
            margin:0 100px;
        }
        .content{
            .switch{
                bottom: 10px;
                line-height: 18px;
                span{
                    width: 18px;
                    height: 18px;
                }
            }
        }
        .experience-item{
            padding:30px*@scale 30px*@scale 60px*@scale 30px*@scale;
            .title{
                line-height: 50px*@scale;
                font-size: 26px*@scale;
            }
            time{
                font-size: 16px*@scale;
                line-height: 40px*@scale;
            }
            .info{
                .post{
                    font-size: 18px*@scale;
                    line-height: 40px*@scale;
                }
                span{
                    font-size: 17px*@scale;
                }
            }
        }
        .switch-year{
            margin-top: 50px*@scale;
            a{
                width: 60px*@scale;
                height: 30px*@scale;
                line-height: 30px*@scale;
                font-size: 16px*@scale;
            }
        }
    }
    @media all and (min-width:1180px){
        .content-container{
            margin: 0;
        }
        .content{
            width: 600px;
            margin: 0 auto;
        }
    }
</style>
